import React from 'react';
import './sidebar.css';
import List from './list';
import {drawerChange} from '../reducers/sidebar';
import { connect } from 'react-redux';
import { slideInLeft, slideOutLeft } from 'react-animations';
import Radium, {StyleRoot} from 'radium';
import ReactDOM from 'react-dom';

const styles = {
    slideInLeft: {
      animation: 'x 1s',
      animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    },
    slideOutLeft: {
        animation: 'x 1s',
        animationName: Radium.keyframes(slideOutLeft, 'slideOutLeft')
    }
}

class SideBar extends React.Component {
    state = {
        drawerOpen: false,
        value: false
    }
    openDrawer = () => {
        this.setState((prev) => ({
            value: true,
        }))
        this.props.dispatch( drawerChange({val: true}) )
    }
    closeDrawer = (e) => {
        const tag = e.target.attributes.getNamedItem('data-tag');
        console.log('closeDrawer Fired')
        //SHOULD ONLY FIRE IF STATE IS OPEN
        if (e.target !== this.burgerIcon.current && e.target !== this.list.current && e.target !== this.listUl.current && tag === null) {
            console.log('inside conditions')
            this.setState((prev) => ({
                value: false,
            }))
            setTimeout(() => {
                this.props.dispatch( drawerChange({val: false}) )
            }, 1000)
        }
    }
    componentDidMount() {
        const {parent} = this.props;
        const parentEl = parent !== null ? this.props.parent : null;
        setTimeout(() => {
            const tryThis = ReactDOM.findDOMNode(parentEl.current)
            parentEl.current.addEventListener('click', (e) => this.closeDrawer(e))
        }, 2000)
    }
    list = React.createRef();
    listUl = React.createRef();
    burgerIcon = React.createRef();
    render() {
        const {drawerOpen, value} = this.state;
        const {sidebar} = this.props;
        console.log('value2', value, 'sidebar', sidebar)
        return (
            <React.Fragment>
            {!sidebar &&
            <StyleRoot>
            <div
            className="burgerIcon"
            ref={this.burgerIcon}
            // style={styles.slideInLeft}
            style={{transform: 'rotate(90deg)', fontSize: '40px', cursor: 'pointer', marginTop: '-5px', marginLeft: '15px'}}
            onClick={this.openDrawer}
            >
              |||
            </div>
            </StyleRoot>
            }
            {sidebar &&
            <React.Fragment>
            <StyleRoot>
            <div style={(value) ? styles.slideInLeft : styles.slideOutLeft} className="blur"></div>
            
            <div
            onClick={this.openDrawer}
            className="list"
            style={(value) ? styles.slideInLeft : styles.slideOutLeft}
            ref={this.list}
            >
                <ul ref={this.listUl}>
                    <List parent={this.listUl} />
                </ul>
                <button
                className="logout"
                >Logout</button>
              
            </div>
            </StyleRoot>
            </React.Fragment>
            
            }
            </React.Fragment>
        )
    }
}

export default connect((state) => ({
    sidebar: state.sidebar
}))(SideBar);